import {
  ChangeDetectionStrategy,
  Component,
  ContentChildren,
  Input,
  QueryList,
  TemplateRef,
  ViewEncapsulation
} from '@angular/core';
import { CmacsOptionComponent } from './cmacs-option.component';

@Component({
  selector: 'nz-option-group',
  exportAs: 'nzOptionGroup',
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
  templateUrl: './cmacs-option-group.component.html'
})
export class CmacsOptionGroupComponent {
  isLabelString = false;
  label: string | TemplateRef<void>;
  @ContentChildren(CmacsOptionComponent) listOfNzOptionComponent: QueryList<CmacsOptionComponent>;

  @Input()
  set nzLabel(value: string | TemplateRef<void>) {
    this.label = value;
    this.isLabelString = !(this.nzLabel instanceof TemplateRef);
  }

  get nzLabel(): string | TemplateRef<void> {
    return this.label;
  }
}
