import { FocusMonitor } from '@angular/cdk/a11y';
import { Direction, Directionality } from '@angular/cdk/bidi';
import {
  forwardRef,
  AfterViewInit,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ElementRef,
  EventEmitter,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Optional,
  Output,
  Renderer2,
  SimpleChanges,
  ViewChild,
  ViewEncapsulation
} from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';

import { CmacsCheckboxWrapperComponent } from './cmacs-checkbox-wrapper.component';
import { takeUntil } from 'rxjs/operators';
import { Subject } from 'rxjs';
import { InputBoolean } from 'ng-zorro-antd/core/util';
import { BooleanInput, NzSafeAny, OnChangeType, OnTouchedType } from 'ng-zorro-antd/core/types';

export type CmacsTheme = 'light' | 'dark';

@Component({
  selector: '[cmacs-checkbox]',
  exportAs: 'cmacsCheckbox',
  preserveWhitespaces: false,
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  templateUrl: './cmacs-checkbox.component.html',
  styleUrls: ['./cmacs-checkbox.component.css'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => CmacsCheckboxComponent),
      multi: true
    }
  ],
  host: {
    '[class.ant-checkbox-wrapper-checked]': 'checked',
    '[class.ant-checkbox-rtl]': `dir === 'rtl'`,
    '(click)': 'hostClick($event)'
  }
})
export class CmacsCheckboxComponent implements OnInit, ControlValueAccessor, AfterViewInit, OnDestroy {
  static ngAcceptInputType_autoFocus: BooleanInput;
  static ngAcceptInputType_disabled: BooleanInput;
  static ngAcceptInputType_indeterminate: BooleanInput;
  static ngAcceptInputType_checked: BooleanInput;
  static ngAcceptInputType_theme: string;

  dir: Direction = 'ltr';
  private destroy$ = new Subject<void>();

  onChange: OnChangeType = () => { };
  onTouched: OnTouchedType = () => { };
  @ViewChild('inputElement', { static: true }) private inputElement: ElementRef;
  @Output() readonly checkedChange = new EventEmitter<boolean>();
  @Input() value: NzSafeAny;
  @Input() theme: CmacsTheme = 'light';
  @Input() @InputBoolean() autoFocus = false;
  @Input() @InputBoolean() disabled = false;
  @Input() @InputBoolean() indeterminate = false;
  @Input() @InputBoolean() checked = false;

  hostClick(e: MouseEvent): void {
    e.preventDefault();
    this.focus();
    this.innerCheckedChange(!this.checked);
  }

  innerCheckedChange(checked: boolean): void {
    if (!this.disabled) {
      this.checked = checked;
      this.onChange(this.checked);
      this.checkedChange.emit(this.checked);
      if (this.cmacsCheckboxWrapperComponent) {
        this.cmacsCheckboxWrapperComponent.onChange();
      }
    }
  }

  writeValue(value: boolean): void {
    this.checked = value;
    this.cdr.markForCheck();
  }

  registerOnChange(fn: (_: boolean) => {}): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: () => {}): void {
    this.onTouched = fn;
  }

  setDisabledState(isDisabled: boolean): void {
    this.disabled = isDisabled;
    this.cdr.markForCheck();
  }

  focus(): void {
    this.focusMonitor.focusVia(this.inputElement, 'keyboard');
  }

  blur(): void {
    this.inputElement.nativeElement.blur();
  }

  constructor(
    private elementRef: ElementRef<HTMLElement>,
    private renderer: Renderer2,
    @Optional() private cmacsCheckboxWrapperComponent: CmacsCheckboxWrapperComponent,
    private cdr: ChangeDetectorRef,
    private focusMonitor: FocusMonitor,
    @Optional() private directionality: Directionality
  ) {
    renderer.addClass(elementRef.nativeElement, 'ant-checkbox-wrapper');
  }

  ngOnInit(): void {
    this.focusMonitor.monitor(this.elementRef, true).subscribe(focusOrigin => {
      if (!focusOrigin) {
        Promise.resolve().then(() => this.onTouched());
      }
    });
    if (this.cmacsCheckboxWrapperComponent) {
      this.cmacsCheckboxWrapperComponent.addCheckbox(this);
    }

    this.directionality.change?.pipe(takeUntil(this.destroy$)).subscribe((direction: Direction) => {
      this.dir = direction;
      this.cdr.detectChanges();
    });

    this.dir = this.directionality.value;   
  }

  ngAfterViewInit(): void {
    if (this.autoFocus) {
      this.focus();
    }
  }

  ngOnDestroy(): void {
    this.focusMonitor.stopMonitoring(this.elementRef);
    if (this.cmacsCheckboxWrapperComponent) {
      this.cmacsCheckboxWrapperComponent.removeCheckbox(this);
    }

    this.destroy$.next();
    this.destroy$.complete();
  }
}
