import { Direction, Directionality } from '@angular/cdk/bidi';
import { ChangeDetectionStrategy, ContentChildren, Component, ElementRef, Input, OnDestroy, OnInit, Optional, ViewEncapsulation, QueryList } from '@angular/core';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { InputBoolean } from 'ng-zorro-antd/core/util';
import { CmacsButtonComponent } from './cmacs-button.component';

export type NzButtonGroupSize = 'large' | 'default' | 'small';

@Component({
// tslint:disable-next-line: component-selector
  selector: 'cmacs-button-group',
  exportAs: 'cmacsButtonGroup',
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  preserveWhitespaces: false,
  host: {
    '[class.ant-btn-group-lg]': `size === 'large'`,
    '[class.ant-btn-group-sm]': `size === 'small'`,
    '[class.ant-btn-group-rtl]': `dir === 'rtl'`
  },
  styleUrls: ['./cmacs-button-group.component.css'],
  templateUrl: './cmacs-button-group.component.html'
})
export class CmacsButtonGroupComponent implements OnInit, OnDestroy {
  @Input() size: NzButtonGroupSize = 'default';
  @Input() @InputBoolean() overlap = false;
  @Input() @InputBoolean() disabled = false;
  @Input() @InputBoolean() disabledNav = false;

  dir: Direction = 'ltr';
  private destroy$ = new Subject<void>();
  buttons: CmacsButtonComponent[] = [];
  indexBtn = 0;
  @ContentChildren(CmacsButtonComponent) contentButtons: QueryList<CmacsButtonComponent>;

  constructor(private elementRef: ElementRef, @Optional() private directionality: Directionality) {
    this.elementRef.nativeElement.classList.add('ant-btn-group');
  } 

  ngOnInit(): void {
    this.dir = this.directionality.value;
    this.directionality.change?.pipe(takeUntil(this.destroy$)).subscribe((direction: Direction) => {
      this.dir = direction;
    });
  }

  ngAfterContentInit(): void {
    this.buttons = this.contentButtons.toArray();
    if (this.overlap) {
      for (let index = 1; index < this.buttons.length; index++) {
        this.buttons[index].hideBtn();
      }
    }
    if (this.disabled) {
      for (const iterator of this.buttons) {
        iterator.disabledBtn();
      }
    }
  }

  moveLeft(): void {
    this.buttons[this.indexBtn--].hideBtn();
    this.buttons[this.indexBtn].showBtn();
  }

  moveRight(): void {
    this.buttons[this.indexBtn++].hideBtn();
    this.buttons[this.indexBtn].showBtn();
  }

  isDisableLeft(): boolean {
    return this.indexBtn === 0 || this.disabled || this.disabledNav;
  }

  isDisableRight(): boolean {
    return this.indexBtn === this.buttons.length - 1 || this.disabled || this.disabledNav;
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }
}
