import { FocusMonitor } from '@angular/cdk/a11y';
import { Direction, Directionality } from '@angular/cdk/bidi';
import {
  AfterContentInit,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ContentChildren,
  Directive,
  ElementRef,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Optional,
  QueryList,
  SimpleChanges,
  TemplateRef,
  ViewEncapsulation
} from '@angular/core';
import { BooleanInput, NzSizeLDSType } from 'ng-zorro-antd/core/types';
import { InputBoolean } from 'ng-zorro-antd/core/util';
import { merge, Subject } from 'rxjs';
import { map, mergeMap, startWith, switchMap, takeUntil } from 'rxjs/operators';
import { CmacsInputDirective } from './cmacs-input.directive';

@Component({
// tslint:disable-next-line: component-selector
  selector: 'cmacs-input-group',
  exportAs: 'cmacsInputGroup',
  preserveWhitespaces: false,
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
  templateUrl: './cmacs-input-group.component.html',
// tslint:disable-next-line: use-host-property-decorator
  host: {
    '[class.ant-input-group-compact]': `compact`,
    '[class.ant-input-search-enter-button]': `search`,
    '[class.ant-input-search]': `search`,
    '[class.ant-input-search-rtl]': `dir === 'rtl'`,
    '[class.ant-input-search-sm]': `search && isSmall`,
    '[class.ant-input-search-large]': `search && isLarge`,
    '[class.ant-input-group-wrapper]': `isAddOn`,
    '[class.ant-input-group-wrapper-rtl]': `dir === 'rtl'`,
    '[class.ant-input-group-wrapper-lg]': `isAddOn && isLarge`,
    '[class.ant-input-group-wrapper-sm]': `isAddOn && isSmall`,
    '[class.ant-input-affix-wrapper]': `isAffix && !isAddOn`,
    '[class.ant-input-affix-wrapper-rtl]': `dir === 'rtl'`,
    '[class.ant-input-affix-wrapper-focused]': `isAffix && focused`,
    '[class.ant-input-affix-wrapper-disabled]': `isAffix && disabled`,
    '[class.ant-input-affix-wrapper-lg]': `isAffix && !isAddOn && isLarge`,
    '[class.ant-input-affix-wrapper-sm]': `isAffix && !isAddOn && isSmall`,
    '[class.ant-input-group]': `!isAffix && !isAddOn`,
    '[class.ant-input-group-rtl]': `dir === 'rtl'`,
    '[class.ant-input-group-lg]': `!isAffix && !isAddOn && isLarge`,
    '[class.ant-input-group-sm]': `!isAffix && !isAddOn && isSmall`
  }
})
export class CmacsInputGroupComponent implements AfterContentInit {

  @ContentChildren(CmacsInputDirective) listOfNzInputDirective!: QueryList<CmacsInputDirective>;
  @Input() cmacsAddOnBeforeIcon?: string | null = null;
  @Input() cmacsAddOnAfterIcon?: string | null = null;
  @Input() cmacsPrefixIcon?: string | null = null;
  @Input() cmacsSuffixIcon?: string | null = null;
  @Input() cmacsAddOnBefore?: string | TemplateRef<void>;
  @Input() cmacsAddOnAfter?: string | TemplateRef<void>;
  @Input() cmacsPrefix?: string | TemplateRef<void>;
  @Input() cmacsSuffix?: string | TemplateRef<void>;
  @Input() cmacsSize: NzSizeLDSType = 'default';
  @Input() @InputBoolean() search = false;
  @Input() @InputBoolean() compact = false;
  isLarge = false;
  isSmall = false;
  isAffix = false;
  isAddOn = false;
  focused = false;
  disabled = false;
  dir: Direction = 'ltr';
  private destroy$ = new Subject<void>();

  constructor(
    private focusMonitor: FocusMonitor,
    private elementRef: ElementRef,
    private cdr: ChangeDetectorRef,
    @Optional() private directionality: Directionality
  ) { }

  updateChildrenInputSize(): void {
    if (this.listOfNzInputDirective) {
      this.listOfNzInputDirective.forEach(item => (item.size = this.cmacsSize));
    }
  }

  ngOnInit(): void {
    this.focusMonitor
      .monitor(this.elementRef, true)
      .pipe(takeUntil(this.destroy$))
      .subscribe(focusOrigin => {
        this.focused = !!focusOrigin;
        this.cdr.markForCheck();
      });

    this.dir = this.directionality.value;
    this.directionality.change?.pipe(takeUntil(this.destroy$)).subscribe((direction: Direction) => {
      this.dir = direction;
    });
  }

  ngAfterContentInit(): void {
    this.updateChildrenInputSize();
    const listOfInputChange$ = this.listOfNzInputDirective.changes.pipe(startWith(this.listOfNzInputDirective));
    listOfInputChange$
      .pipe(
        switchMap(list => {
          return merge(...[listOfInputChange$, ...list.map((input: CmacsInputDirective) => input.disabled$)]);
        }),
        mergeMap(() => listOfInputChange$),
        map(list => list.some((input: CmacsInputDirective) => input.disabled)),
        takeUntil(this.destroy$)
      )
      .subscribe(disabled => {
        this.disabled = disabled;
        this.cdr.markForCheck();
      });
  }
  ngOnChanges(changes: SimpleChanges): void {
    const {
      cmacsSize,
      cmacsSuffix,
      cmacsPrefix,
      cmacsPrefixIcon,
      cmacsSuffixIcon,
      cmacsAddOnAfter,
      cmacsAddOnBefore,
      cmacsAddOnAfterIcon,
      cmacsAddOnBeforeIcon
    } = changes;
    if (cmacsSize) {
      this.updateChildrenInputSize();
      this.isLarge = this.cmacsSize === 'large';
      this.isSmall = this.cmacsSize === 'small';
    }
    if (cmacsSuffix || cmacsPrefix || cmacsPrefixIcon || cmacsSuffixIcon) {
      this.isAffix = !!(this.cmacsSuffix || this.cmacsPrefix || this.cmacsPrefixIcon || this.cmacsSuffixIcon);
    }
    if (cmacsAddOnAfter || cmacsAddOnBefore || cmacsAddOnAfterIcon || cmacsAddOnBeforeIcon) {
      this.isAddOn = !!(this.cmacsAddOnAfter || this.cmacsAddOnBefore || this.cmacsAddOnAfterIcon || this.cmacsAddOnBeforeIcon);
    }
  }
  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }
}
