import { Direction, Directionality } from '@angular/cdk/bidi';
import {
  AfterContentInit,
  AfterViewInit,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ContentChild,
  ElementRef,
  Input,
  OnChanges,
  OnDestroy,
  ViewChild,
  OnInit,
  Optional,
  Renderer2,
  SimpleChanges,
  ViewEncapsulation
} from '@angular/core';
import { NzConfigKey, NzConfigService, WithConfig } from 'ng-zorro-antd/core/config';
import { warnDeprecation } from 'ng-zorro-antd/core/logger';
import { BooleanInput } from 'ng-zorro-antd/core/types';
import { InputBoolean } from 'ng-zorro-antd/core/util';

import { NzIconDirective } from 'ng-zorro-antd/icon';
import { Subject } from 'rxjs';
import { filter, startWith, takeUntil } from 'rxjs/operators';

/**
 * @deprecated `danger` not supported, use `nzDanger` instead
 * @breaking-change 12.0.0
 */
type NzLegacyButtonType = 'primary' | 'default' | 'dashed' | 'danger' | 'link' | 'text' | null;

export type NzButtonType = NzLegacyButtonType;
export type NzButtonShape = 'circle' | 'round' | null;
export type NzButtonSize = 'large' | 'default' | 'small';

const NZ_CONFIG_MODULE_NAME: NzConfigKey = 'button';

@Component({
// tslint:disable-next-line: component-selector
  selector: 'button[cmacs-button], a[cmacs-button]',
  exportAs: 'cmacsButton',
  preserveWhitespaces: false,
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  templateUrl: './cmacs-button.component.html',
  styleUrls: ['./cmacs-button.component.css'],
  host: {
    '[class.ant-btn-primary]': `type === 'primary'`,
    '[class.ant-btn-default]': `type === 'default'`,
    '[class.ant-btn-dashed]': `type === 'dashed'`,
    '[class.ant-btn-link]': `type === 'link'`,
    '[class.ant-btn-text]': `type === 'text'`,
    '[class.ant-btn-danger]': `type === 'danger'`,
    '[class.ant-btn-circle]': `shape === 'circle'`,
    '[class.ant-btn-round]': `shape === 'round'`,
    '[class.ant-btn-lg]': `size === 'large'`,
    '[class.ant-btn-sm]': `size === 'small'`,
    '[class.ant-btn-dangerous]': `danger`,
    '[class.ant-btn-loading]': `loading`,
    '[class.ant-btn-background-ghost]': `ghost`,
    '[class.ant-btn-block]': `block`,
    '[class.ant-input-search-button]': `search`,
    '[class.cmacs-btn-action]': `action`,
    '[class.ant-btn-rtl]': `dir === 'rtl'`,
    '[attr.tabindex]': 'disabled ? -1 : (tabIndex === null ? null : tabIndex)',
    '[attr.disabled]': 'disabled || null'
  }
})
export class CmacsButtonComponent implements AfterContentInit, AfterViewInit, OnInit, OnDestroy, OnChanges {

  @ContentChild(NzIconDirective, { read: ElementRef }) nzIconDirectiveElement!: ElementRef;
  @Input() @InputBoolean() block: boolean = false;
  @Input() @InputBoolean() ghost: boolean = false;
  @Input() @InputBoolean() search: boolean = false;
  @Input() @InputBoolean() loading: boolean = false;
  @Input() @InputBoolean() danger: boolean = false;
  @Input() @InputBoolean() disabled: boolean = false;
  @Input() tabIndex: number | string | null = null;
  @Input() type: NzButtonType = 'default';
  @Input() shape: NzButtonShape = null;
  @Input() @WithConfig() size: NzButtonSize = 'default';
  @Input() @InputBoolean() action = false;
  dir: Direction = 'ltr';
  private destroy$ = new Subject<void>();
  private loading$ = new Subject<boolean>();
  @ViewChild('contentElement') contentElement: ElementRef;

  insertSpan(nodes: NodeList, renderer: Renderer2): void {
    nodes.forEach(node => {
      if (node.nodeName === '#text') {
        const span = renderer.createElement('span');
        const parent = renderer.parentNode(node);
        renderer.insertBefore(parent, span, node);
        renderer.appendChild(span, node);
      }
    });
  }

  assertIconOnly(element: HTMLButtonElement, renderer: Renderer2): void {
    const listOfNode = Array.from(element.childNodes);
    const iconCount = listOfNode.filter(node => node.nodeName === 'I').length;
    const noText = listOfNode.every(node => node.nodeName !== '#text');
    const noSpan = listOfNode.every(node => node.nodeName !== 'SPAN');
    const isIconOnly = noSpan && noText && iconCount >= 1;
    if (isIconOnly) {
      renderer.addClass(element, 'ant-btn-icon-only');
    }
  }

  constructor(
    private elementRef: ElementRef,
    private cdr: ChangeDetectorRef,
    private renderer: Renderer2,
    public nzConfigService: NzConfigService,
    @Optional() private directionality: Directionality
  ) {
    this.renderer.addClass(elementRef.nativeElement, 'ant-btn');
    this.nzConfigService
      .getConfigChangeEventForComponent(NZ_CONFIG_MODULE_NAME)
      .pipe(takeUntil(this.destroy$))
      .subscribe(() => {
        this.cdr.markForCheck();
      });
  }

  ngOnInit(): void {
    this.directionality.change?.pipe(takeUntil(this.destroy$)).subscribe((direction: Direction) => {
      this.dir = direction;
      this.cdr.detectChanges();
    });

    this.dir = this.directionality.value;
  }

  ngOnChanges(changes: SimpleChanges): void {
    const { loading, type } = changes;
    if (loading) {
      this.loading$.next(this.loading);
    }
  }

  ngAfterViewInit(): void {
    this.assertIconOnly(this.elementRef.nativeElement, this.renderer);
    this.insertSpan(this.elementRef.nativeElement.childNodes, this.renderer);
    this.imageWrapper();
  }

  imageWrapper(): void {
    const element = this.elementRef.nativeElement as HTMLButtonElement;
    const listOfNode = Array.from(element.childNodes);
    const iconList = listOfNode.filter(node => node.nodeName === 'I');

    iconList.forEach(icon => {
      this.renderer.addClass(icon, 'iconspan');
    });
  }

  ngAfterContentInit(): void {
    this.loading$
      .pipe(
        startWith(this.loading),
        filter(() => !!this.nzIconDirectiveElement),
        takeUntil(this.destroy$)
      )
      .subscribe(loading => {
        const nativeElement = this.nzIconDirectiveElement.nativeElement;
        if (loading) {
          this.renderer.setStyle(nativeElement, 'display', 'none');
        } else {
          this.renderer.removeStyle(nativeElement, 'display');
        }
      });
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }

  hideBtn(): void {
    this.elementRef.nativeElement.style.display = 'none';
  }

  showBtn(): void {
    this.elementRef.nativeElement.style.display = 'inline-block';
  }

  disabledBtn(): void {
    this.elementRef.nativeElement.disabled = true;
  }
  
}
